'use client'

import { useState } from 'react'
import { useParams, useRouter } from 'next/navigation'
import Image from 'next/image'
import { products } from '@/data/products'
import { useCart } from '@/context/CartContext'
import { Button } from '@/components/ui/button'
import { Card, CardContent } from '@/components/ui/card'
import { Badge } from '@/components/ui/badge'
import { Separator } from '@/components/ui/separator'

export default function ProductDetailPage() {
  const params = useParams()
  const router = useRouter()
  const { addItem } = useCart()
  const [quantity, setQuantity] = useState(1)
  const [isAdding, setIsAdding] = useState(false)

  const product = products.find(p => p.id === params.id)

  if (!product) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="text-center">
          <h1 className="text-2xl font-bold text-gray-900 mb-4">
            Producto no encontrado
          </h1>
          <p className="text-gray-600 mb-6">
            El producto que buscas no existe o ha sido eliminado.
          </p>
          <Button 
            onClick={() => router.push('/productos')}
            className="bg-red-600 hover:bg-red-700"
          >
            Volver a Productos
          </Button>
        </div>
      </div>
    )
  }

  const formatPrice = (price: number) => {
    return new Intl.NumberFormat('es-AR', {
      style: 'currency',
      currency: 'ARS'
    }).format(price)
  }

  const handleAddToCart = async () => {
    setIsAdding(true)
    
    // Simular un pequeño delay para mejor UX
    await new Promise(resolve => setTimeout(resolve, 500))
    
    for (let i = 0; i < quantity; i++) {
      addItem(product)
    }
    
    setIsAdding(false)
    // Aquí podrías mostrar una notificación de éxito
  }

  const handleQuantityChange = (newQuantity: number) => {
    if (newQuantity >= 1 && newQuantity <= product.stock) {
      setQuantity(newQuantity)
    }
  }

  const relatedProducts = products
    .filter(p => p.categoria === product.categoria && p.id !== product.id)
    .slice(0, 3)

  return (
    <div className="min-h-screen bg-gray-50">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {/* Breadcrumb */}
        <nav className="mb-8">
          <div className="flex items-center space-x-2 text-sm text-gray-600">
            <button 
              onClick={() => router.push('/')}
              className="hover:text-red-600"
            >
              Inicio
            </button>
            <span>/</span>
            <button 
              onClick={() => router.push('/productos')}
              className="hover:text-red-600"
            >
              Productos
            </button>
            <span>/</span>
            <span className="text-gray-900">{product.nombre}</span>
          </div>
        </nav>

        {/* Product Detail */}
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-12 mb-16">
          {/* Product Image */}
          <div className="relative">
            <div className="aspect-square relative rounded-lg overflow-hidden bg-white">
              <Image
                src={product.imagen}
                alt={product.nombre}
                fill
                className="object-cover"
                priority
              />
              <Badge 
                className="absolute top-4 left-4 bg-red-600 hover:bg-red-700"
              >
                {product.categoria}
              </Badge>
            </div>
          </div>

          {/* Product Info */}
          <div className="space-y-6">
            <div>
              <h1 className="text-3xl font-bold text-gray-900 mb-2">
                {product.nombre}
              </h1>
              <div className="flex items-center gap-4 mb-4">
                <span className="text-3xl font-bold text-red-600">
                  {formatPrice(product.precio)}
                </span>
                <Badge variant="outline" className="text-green-600 border-green-600">
                  {product.stock > 0 ? `${product.stock} disponibles` : 'Sin stock'}
                </Badge>
              </div>
            </div>

            <Separator />

            <div>
              <h3 className="text-lg font-semibold mb-3">Descripción</h3>
              <p className="text-gray-700 leading-relaxed">
                {product.descripcion}
              </p>
            </div>

            <Separator />

            {/* Quantity and Add to Cart */}
            <div className="space-y-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Cantidad
                </label>
                <div className="flex items-center space-x-3">
                  <Button
                    variant="outline"
                    size="sm"
                    onClick={() => handleQuantityChange(quantity - 1)}
                    disabled={quantity <= 1}
                  >
                    -
                  </Button>
                  <span className="w-12 text-center font-medium">
                    {quantity}
                  </span>
                  <Button
                    variant="outline"
                    size="sm"
                    onClick={() => handleQuantityChange(quantity + 1)}
                    disabled={quantity >= product.stock}
                  >
                    +
                  </Button>
                </div>
              </div>

              <div className="flex flex-col sm:flex-row gap-3">
                <Button
                  onClick={handleAddToCart}
                  disabled={product.stock === 0 || isAdding}
                  className="flex-1 bg-red-600 hover:bg-red-700 text-white py-3"
                  size="lg"
                >
                  {isAdding ? 'Agregando...' : 'Agregar al Carrito'}
                </Button>
                <Button
                  variant="outline"
                  onClick={() => router.push('/productos')}
                  className="flex-1 py-3"
                  size="lg"
                >
                  Seguir Comprando
                </Button>
              </div>
            </div>

            {/* Product Features */}
            <Card>
              <CardContent className="p-6">
                <h3 className="font-semibold mb-4">Información del Producto</h3>
                <div className="space-y-3 text-sm">
                  <div className="flex justify-between">
                    <span className="text-gray-600">Categoría:</span>
                    <span className="font-medium">{product.categoria}</span>
                  </div>
                  <div className="flex justify-between">
                    <span className="text-gray-600">Stock disponible:</span>
                    <span className="font-medium">{product.stock} unidades</span>
                  </div>
                  <div className="flex justify-between">
                    <span className="text-gray-600">Envío:</span>
                    <span className="font-medium text-green-600">Gratis en CABA</span>
                  </div>
                </div>
              </CardContent>
            </Card>
          </div>
        </div>

        {/* Related Products */}
        {relatedProducts.length > 0 && (
          <div>
            <h2 className="text-2xl font-bold text-gray-900 mb-8">
              Productos Relacionados
            </h2>
            <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6">
              {relatedProducts.map((relatedProduct) => (
                <Card key={relatedProduct.id} className="hover:shadow-lg transition-shadow">
                  <div className="relative h-48">
                    <Image
                      src={relatedProduct.imagen}
                      alt={relatedProduct.nombre}
                      fill
                      className="object-cover rounded-t-lg"
                    />
                  </div>
                  <CardContent className="p-4">
                    <h3 className="font-semibold mb-2 line-clamp-2">
                      {relatedProduct.nombre}
                    </h3>
                    <div className="flex justify-between items-center">
                      <span className="text-lg font-bold text-red-600">
                        {formatPrice(relatedProduct.precio)}
                      </span>
                      <Button
                        size="sm"
                        onClick={() => router.push(`/producto/${relatedProduct.id}`)}
                        variant="outline"
                      >
                        Ver
                      </Button>
                    </div>
                  </CardContent>
                </Card>
              ))}
            </div>
          </div>
        )}
      </div>
    </div>
  )
}
